<template>
  <section>
    <div class="navbar-container">
      <div class="container-fluid">
        <div class="navbar-header">
           <span class="page-title">报警规则</span>
        </div>
      </div>
    </div>
    <div class="navbar-container box-shadow">
      <div class="container-fluid ">
          <hr class="normal"/>
          <div class="navbar-header clearfix">
              <ul class="flex pull-right">
                  <!-- <li>
                      <el-button class="cds-button" type="text">新建规则</el-button>
                  </li> -->
                  <li v-if="!clickBound">
                      <el-button class="cds-button" type="text" @click="FnOperateRules">绑定规则</el-button>
                  </li>
                  <li v-else>
                      <el-button class="cds-button" type="text" @click="FnGetRulesList('remove')">移除规则</el-button>
                  </li>
              </ul>
          </div>
      </div>
    </div>
    <div class="content">
      <el-table class="table--transparent" :data="list">
          <el-table-column label="规则名称" prop="name"></el-table-column>
          <el-table-column label="状态" prop="status_str"></el-table-column>
          <el-table-column label="启用">
             <template slot-scope="scope">
              <span v-if="scope.row.is_active == 1">已启用</span><span v-else>已禁用</span>
            </template>
          </el-table-column>
          <el-table-column label="监控项" prop="metrics_id_str"></el-table-column>
          <el-table-column label="报警规则">
            <template slot-scope="scope">
              {{scope.row.period_str}} {{scope.row.lasted_period_str}} {{scope.row.metrics_id_str}} {{scope.row.trigger_type_str}} 
              {{scope.row.trigger_symbol}}{{scope.row.value}}{{scope.row.unit}} 则发"{{scope.row.level_str}}"报警
            </template>
          </el-table-column>
          <el-table-column label="操作">
            <template slot-scope="scope">
              <ul class="flex flex-center">
                <li v-if="clickBound">
                  <span class="light pointer" @click="FnBoundRules(scope.row)">绑定</span>
                </li>
                <li v-if="!clickBound">
                  <span class="light pointer" @click="FnRemoveInstance(scope.row)">移除</span>
                </li>
              </ul>
            </template>
          </el-table-column>
       </el-table>
       <el-pagination class="pull-right" :background="true" @current-change="handleCurrentChange"
          :current-page="currentPage" :page-size="page_size"
          prev-text="上一页" next-text="下一页"
          layout="prev, pager, next, jumper" :total="list_num"></el-pagination>
    </div>
  </section>  
</template>

<script>
import {
  removeInstance,
  boundRules,
  instanceLinkRule,
  instanceNoLinkRule
} from "@/service/alarm/alarm_instance_rule";
import { Message, MessageBox } from "element-ui";
export default {
  components: {},
  data() {
    return {
      list: [],
      currentPage: 1,
      page_size: 20,
      list_num: 0,
      clickBound: false
    };
  },
  created() {
    this.FnGetRulesList('first');
  },
  methods: {
    handleCurrentChange(v) {
      this.currentPage = v;
      this.FnGetRulesList();
    },
    async FnGetRulesList(type) {
      this.list = [];
      let reqData = {
        instance_id: this.$route.params.id,
        page_number: this.currentPage,
        page_size: this.page_size,
        type: this.$route.params.type
      };
      if (type === 'remove') {
        this.clickBound = false;
      }
      let res = this.clickBound ? await instanceNoLinkRule(reqData) : await instanceLinkRule(reqData);
      if (res.code === "Success") {
        this.list_num = res.total_count;
        this.list = res.data;
        if (type === 'first' && this.list.length <= 0) {
          this.clickBound = true;
          this.FnGetRulesList();
        }
        let groupList = [];
        let keyList = [];
        this.list.forEach(item => {
          keyList = Object.keys(item.notification_group);
          keyList.forEach(m => {
            groupList.push({
              id: m,
              name: item.notification_group[m]
            });
          });
          item.group_list = groupList;
        });
      } else {
        Message({ message: res.msg, type: "error" });
      }
    },
    FnOperateRules() {
      this.clickBound = true;
      this.FnGetRulesList();
    },
    FnRemoveInstance(ele) {
      MessageBox.confirm("确认将该实例移除此报警规则吗？", "提示", {
        confirmButtonText: "确认",
        cancelButtonText: "取消",
        type: "warning",
        dangerouslyUseHTMLString: true
      }).then(() => {
        this.confrimRemove(ele);
      });
    },
    async confrimRemove(ele) {
      let ids = [];
      ids.push(ele.id);
      let reqData = {
          rule_ids: ids,
          instance_id: this.$route.params.id
      };
      let res = await removeInstance(reqData);
      if (res.code === "Success") {
        this.FnGetRulesList();
        Message({ message: res.msg, type: "success" });    
      } else {
        Message({ message: res.msg, type: "error" });
      }
    },
    FnBoundRules(ele) {
      MessageBox.confirm("确认该实例应用此报警规则吗？", "提示", {
        confirmButtonText: "确认",
        cancelButtonText: "取消",
        type: "warning",
        dangerouslyUseHTMLString: true
      }).then(() => {
        this.confirmBoundRules(ele);
      });
    },
    async confirmBoundRules(ele) {
      let ids = [];
      ids.push(ele.id);  
      let reqData = {
          rule_ids: ids,
          instance_id: this.$route.params.id,
          product_type: this.$route.params.type,
          instance_name: this.$route.params.name
      };
      let res = await boundRules(reqData);
      if (res.code === "Success") {
        this.FnGetRulesList(); 
        Message({ message: res.msg, type: "success" });  
      } else {
        Message({ message: res.msg, type: "error" });
      }
    }
  }
};
</script>

