import http from "../http";

export function removeInstance (data) {
    return http({
        method: 'post',
        url: '/alarm/rule/delete_instance',
        data: data
    })
}
export function boundRules (data) {
    return http({
        method: 'post',
        url: '/alarm/rule/add_instance',
        data: data
    })
}

export function instanceLinkRule (data) {
    return http({
        method: 'post',
        url: '/alarm/rule/instance_link_rule',
        data: data
    })
}
export function instanceNoLinkRule (data) {
    return http({
        method: 'post',
        url: '/alarm/rule/instance_nolink_rule',
        data: data
    })
}